import type { CSSProperties } from '@stitches/react'

export const utils = {
  paddingX: (value: CSSProperties['padding']) => ({
    paddingLeft: value,
    paddingRight: value,
  }),
  paddingY: (value: CSSProperties['padding']) => ({
    paddingTop: value,
    paddingBottom: value,
  }),
  marginX: (value: CSSProperties['margin']) => ({
    marginLeft: value,
    marginRight: value,
  }),
  marginY: (value: CSSProperties['margin']) => ({
    marginTop: value,
    marginBottom: value,
  }),
  square: (value: CSSProperties['width']) => ({
    width: value,
    height: value,
  }),
  _hover: (css: CSSProperties) => ({
    '&:hover, &[data-hovered]': css,
  }),
}
