import { useCallback, useEffect, useRef } from 'react'

type Listener = <
  KW extends keyof WindowEventMap,
  KH extends keyof HTMLElementEventMap,
  K extends keyof DocumentEventMap
>(
  element: EventTarget,
  eventName: KW | KH | K | string,
  listener: (
    event:
      | WindowEventMap[KW]
      | HTMLElementEventMap[KH]
      | DocumentEventMap[K]
      | Event
  ) => void,
  options?: boolean | AddEventListenerOptions
) => void

export interface Listeners {
  addListener: Listener
  removeListener: Listener
  removeAllListeners: () => void
}

export const useListeners = (): Listeners => {
  const globalListeners = useRef(new Map())

  const removeListener: Listener = useCallback(
    (eventTarget, type, listener, options) => {
      const fnGlobalListener = globalListeners.current.get(listener)?.fn
      const fn = fnGlobalListener ?? listener
      eventTarget.removeEventListener(type, fn, options)
      globalListeners.current.delete(listener)
    },
    []
  )

  const addListener: Listener = useCallback(
    (eventTarget, type, listener, options) => {
      // Make sure we remove the listener after it is called with the `once` option.
      const fn = (typeof options === 'object' ? options.once === true : false)
        ? (...args: any[]) => {
            // @ts-expect-error
            listener(...args)
            removeListener(eventTarget, type, listener, options)
            globalListeners.current.delete(listener)
          }
        : listener

      globalListeners.current.set(listener, {
        type,
        eventTarget,
        fn,
        options,
      })
      eventTarget.addEventListener(type, fn, options)
    },
    [removeListener]
  )

  const removeAllListeners = useCallback(() => {
    globalListeners.current.forEach((value, key) => {
      removeListener(value.eventTarget, value.type, key, value.options)
    })
  }, [removeListener])

  useEffect(() => removeAllListeners, [removeAllListeners])

  return { addListener, removeListener, removeAllListeners }
}
