import { createStitches } from '@stitches/react'
import type * as CSSUtil from '@stitches/react/types/css-util'

import { utils, theme as defaultTheme, themeMap, media } from './config'

const stitches = createStitches({
  theme: defaultTheme,
  media,
  utils,
  themeMap,
})

export const {
  config,
  createTheme,
  css,
  getCssText,
  globalCss,
  keyframes,
  prefix,
  styled,
  theme,
} = stitches

export type FontFace = CSSUtil.Native.AtRule.FontFace

export const fontFace = (...fonts: FontFace[]): string =>
  globalCss({
    '@font-face': fonts,
  })()
