import React from 'react'
import type {
  CSSProperties,
  ComponentType,
  ElementRef,
  ElementType,
  ExoticComponent,
  ForwardRefExoticComponent,
  JSXElementConstructor,
  PropsWithoutRef,
  RefAttributes,
} from 'react'
import type { RemoveIndex } from '@stitches/react/types/stitches'
import type { Token } from '@stitches/react/types/theme'
import type {
  StyledComponentProps as StitchesStyledComponentProps,
  $$StyledComponentMedia,
  $$StyledComponentProps,
  $$StyledComponentType,
  TransformProps,
} from '@stitches/react/types/styled-component'
import type * as Util from '@stitches/react/types/util'
import type { OmitFromUnion } from '@mirohq/design-system-types'

import * as stitches from './stitches'
import type { CSS, StyledComponentProps, ForbiddenProps } from './types'

// Error messages
// -----------------------------------------------------------------------------

export const AS_ERROR =
  'Polymorphism via `as` prop is not available in styled components.'

export const STYLING_ATTRS_ERROR =
  'The `className` and `style` attributes are not avaialable in styled components.'

// Utils
// -----------------------------------------------------------------------------

const styleWithCssVars = (
  style: CSS,
  themeMap: typeof stitches.config.themeMap
): CSSProperties =>
  (Object.entries(style) as Array<[string, unknown]>).reduce(
    (acc, [prop, value]) => {
      // @ts-expect-error
      const mapKeyForProp = themeMap[prop]
      const mapping = stitches.theme[mapKeyForProp] as unknown as {
        [Prop: string]: { [K in keyof Token]: Token[K] }
      }

      const parsedValue =
        typeof value === 'string' && /^\$.+$/.test(value)
          ? mapping[value.replace('$', '')]?.computedValue
          : value

      return {
        ...acc,
        [prop]: parsedValue,
      }
    },
    {}
  ) as CSSProperties

// Styled
// -----------------------------------------------------------------------------

type Variants<T> = 'variants' extends keyof T
  ? {
      [Name in keyof T['variants']]?:
        | Util.Widen<keyof T['variants'][Name]>
        | Util.String
    }
  : Util.WideObject

type Styles<Composers> = Composers extends
  | string
  | ComponentType<any>
  | Util.Function
  ? Composers
  : RemoveIndex<CSS> & {
      variants?: {
        [Name in string]: {
          [Pair in number | string]: CSS
        }
      }
      compoundVariants?: Array<Variants<Composers> & { css: CSS }>
      defaultVariants?: Variants<Composers>
    } & CSS & {
        [K in keyof Composers]: K extends
          | 'compoundVariants'
          | 'defaultVariants'
          | 'variants'
          ? unknown
          : K extends keyof CSS
          ? CSS[K]
          : unknown
      }

// This interface is only needed to fix TS4023
export interface StitchesInternals<
  Type extends
    | keyof JSX.IntrinsicElements
    | React.ComponentType<any>
    | Util.Function,
  Props extends {},
  Media extends typeof stitches.config.media
> {
  className: string
  selector: string
  [$$StyledComponentType]: Type
  [$$StyledComponentProps]: Props
  [$$StyledComponentMedia]: Media
}

export interface CustomStylesProps {
  css?: CSS
  UNSAFE_style?: CSS
}

// TS is infering the return type correctly, no need to make things uglier here
// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
export const styled = <
  Element extends
    | keyof JSX.IntrinsicElements
    | React.ComponentType<any>
    | Util.Function = 'span',
  Composers extends
    | string
    | ExoticComponent<any>
    | JSXElementConstructor<any>
    | Util.Function
    | { [name: string]: unknown } = {}
>(
  element: Element,
  composers?: Styles<Composers>
) => {
  const StyledComponent = stitches.styled(element, composers ?? {})

  type __Element = Element extends ElementType ? Element : any
  type StyledElementRef = ElementRef<__Element>
  type StitchesProps = StitchesStyledComponentProps<[Composers]>
  type VariantProps = TransformProps<
    StitchesProps,
    typeof stitches.config.media
  >
  type ComponentProps = StyledComponentProps<__Element>

  type Props = OmitFromUnion<ComponentProps, keyof VariantProps> &
    VariantProps &
    CustomStylesProps

  const Component = React.forwardRef<StyledElementRef, Props>(
    (props, forwardRef) => {
      const {
        as,
        className,
        style,
        UNSAFE_style, // eslint-disable-line @typescript-eslint/naming-convention
        ...restProps
      } = props as Props & {
        [K in ForbiddenProps]?: unknown
      }

      // based on https://github.com/modulz/stitches/blob/v1.2.8/packages/core/src/features/css.js#L71
      const onlyStyledClasses =
        typeof className === 'string'
          ? className
              ?.split(' ')
              .filter((x: string) => x.match(`${stitches.prefix}c-.+`))
              .join(' ')
          : ''

      const parsedStyle =
        UNSAFE_style !== undefined
          ? styleWithCssVars(UNSAFE_style, stitches.config.themeMap)
          : onlyStyledClasses !== ''
          ? style
          : undefined

      if (
        (className !== undefined && onlyStyledClasses !== className) ||
        (typeof style === 'object' &&
          style !== null &&
          Object.keys(style).length > 0 &&
          onlyStyledClasses === '')
      ) {
        console.error(STYLING_ATTRS_ERROR)
      }

      if (as !== undefined) {
        console.error(AS_ERROR)
      }

      return (
        <StyledComponent
          {...(restProps as any)}
          className={onlyStyledClasses}
          style={parsedStyle}
          ref={forwardRef}
        />
      )
    }
  ) as ForwardRefExoticComponent<
    PropsWithoutRef<Props> & RefAttributes<StyledElementRef>
  > &
    StitchesInternals<Element, StitchesProps, typeof stitches.config.media>

  Component.displayName = 'Styled.ForwardRef'
  Component.toString = StyledComponent.toString
  Component.className = StyledComponent.className
  Component.selector = StyledComponent.selector

  return Component
}
