import React from 'react'
import { Slot } from '@radix-ui/react-slot'

export const NODES = [
  // audio
  'audio',

  // container
  'address',
  'article',
  'aside',
  'canvas',
  'div',
  'footer',
  'header',
  'main',
  'nav',
  'nav',
  'section',
  'span',

  // form
  'button',
  'fieldset',
  'form',
  'input',
  'label',
  'option',
  'select',
  'textarea',

  // image
  'figcaption',
  'figure',
  'img',
  'svg',

  // link
  'a',

  // list
  'dd',
  'details',
  'dl',
  'dt',
  'li',
  'menu',
  'ol',
  'summary',
  'ul',

  // typography
  'abbr',
  'blockquote',
  'cite',
  'code',
  'em',
  'h1',
  'h2',
  'h3',
  'h4',
  'h5',
  'h6',
  'kbd',
  'p',
  'pre',
  'small',
  'strong',
  'sub',
  'sup',
  'time',

  // table
  'table',
  'tbody',
  'td',
  'tfoot',
  'th',
  'thead',
  'tr',

  // video
  'video',
] as const

export type PrimitiveProps<E extends React.ElementType> =
  React.ComponentPropsWithRef<E> & {
    asChild?: boolean
  }

export type Primitives = {
  [E in typeof NODES[number]]: React.ForwardRefExoticComponent<
    PrimitiveProps<E>
  >
}

export const Primitive = NODES.reduce((primitive, node) => {
  const Node = React.forwardRef<typeof node, PrimitiveProps<typeof node>>(
    (props, forwardedRef) => {
      const { asChild = false, ...primitiveProps } = props
      const Component = asChild ? Slot : node

      // @ts-expect-error
      return <Component {...primitiveProps} ref={forwardedRef} />
    }
  )

  Node.displayName = `Primitive.${node}`

  return { ...primitive, [node]: Node }
}, {}) as Primitives
