import { isVirtualClick } from '@react-aria/utils'

/**
 * It forces the trigger of pointerDow on the Select.Trigger.
 * This had to be done because when using Ctrl + Option + Space on safari it
 * triggers onClick, and Radix only listens to onPointerDown.
 * https://github.com/radix-ui/primitives/blob/bc28fb42b81e4f95ce41eef3ed683ea0dd823dd8/packages/react/select/src/Select.tsx#L266
 */
export const handleVirtualClick = (e: any, ref: any): void => {
  if (e?.nativeEvent === undefined) {
    console.error(
      'onClick expected a MouseEvent but got different one. It usually happens due to a element used asChild with a different onClick interface.\n' +
        `Event: ${JSON.stringify(e, null, 2)}`
    )
    return
  }

  const nativeEvent = e.nativeEvent

  if (nativeEvent.mozInputSource === undefined) {
    nativeEvent.mozInputSource = null
  }

  // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
  if (isVirtualClick(e.nativeEvent) && ref.current !== null) {
    const pointerMoveEvent = new PointerEvent('pointermove', {
      bubbles: true,
      cancelable: true,
      pointerType: 'mouse',
      isPrimary: true,
      clientX: e.clientX,
      clientY: e.clientY,
    })

    const pointerDownEvent = new PointerEvent('pointerdown', {
      bubbles: true,
      cancelable: true,
      pointerType: 'mouse',
    })

    const pointerUpEvent = new PointerEvent('pointerup', {
      bubbles: true,
      cancelable: true,
      pointerType: 'mouse',
    })

    ref.current.dispatchEvent(pointerMoveEvent)

    ref.current.dispatchEvent(pointerDownEvent)
    ref.current.dispatchEvent(pointerUpEvent)
  }
}
