import type { ReactNode, ReactElement } from 'react'
import { Fragment, Children, cloneElement } from 'react'
import type { DOMEventAttributes } from '@mirohq/design-system-types'

export const isFragment = (fragment: any): boolean =>
  fragment?.type === Fragment || fragment === Fragment

/**
 * Search a specific component by displayName inside of a children and replaces or adds a set of new props.
 * @param children ReactNode where the search for the component by name will take place.
 * @param containsComponent Function validating if props should be overridden.
 * @param props props to be merged with existing props from the targe component.
 * @returns Same children element but with the component and its props replaced if it was found.
 */
export const addPropsToChildren = <T>(
  children: ReactNode,
  containsComponent: (child: ReactElement) => boolean,
  props: Partial<T>
): ReactNode => {
  const numChildren = Children.toArray(children).length

  const executeContainsComponent = (child: ReactElement): ReactElement => {
    if (containsComponent(child)) {
      return cloneElement(child, { ...child.props, ...props })
    }
    return child
  }

  if (numChildren === 1) {
    return executeContainsComponent(children as ReactElement)
  }

  if (numChildren > 1) {
    return Children.map(Children.toArray(children), child =>
      executeContainsComponent(child as ReactElement)
    )
  }

  return children
}

/**
 * Set two refs inside the ref property in order to allow the use of the local ref and an external one
 * @param refs Array with both refs
 * @returns ref property
 */
export const mergeRefs =
  <T = any>(
    refs: Array<React.MutableRefObject<T | null> | React.LegacyRef<T | null>>
  ): React.Ref<T> =>
  value => {
    refs.forEach(ref => {
      if (typeof ref === 'function') {
        ref(value)
      } else if (ref != null) {
        ;(ref as React.MutableRefObject<T | null>).current = value
      }
    })
  }

/**
 * Remove any `on` prop from an object
 * @param props object
 * @param exceptions array string starting with `on`
 * @returns props without keys starting by `on`
 */
export const removeEventProps = <
  T extends {},
  Exceptions extends Array<keyof DOMEventAttributes<any>> = []
>(
  props: T,
  exceptions: Exceptions
): {
  [K in keyof T as K extends Exceptions ? never : K]: T[K]
} =>
  Object.entries(props).reduce((result, [key, fn]) => {
    if (key.startsWith('on') && !exceptions.includes(key as any)) {
      return result
    }

    return {
      ...result,
      [key]: fn,
    }
  }, {}) as T
