import { useEventListener } from '@mirohq/design-system-use-event-listener'
import { useEvent } from '@mirohq/design-system-use-event'
import { useState, useMemo } from 'react'

let KEY_PREFIX = 'MIRO_DS'

function getLocalStorageValue<Value>(key: string, defaultValue: Value): Value {
  if (typeof window === 'undefined') {
    return defaultValue
  }

  try {
    const stored = window.localStorage.getItem(key)
    return stored !== null ? JSON.parse(stored) : defaultValue
  } catch (e) {
    console.error(
      `[useLocalStorage] Error reading localStorage key ${key}:`,
      e
    )
    return defaultValue
  }
}

function setLocalStorageValue<Value>(key: string, value: Value): void {
  if (typeof window === 'undefined') {
    return
  }

  try {
    window.localStorage.setItem(key, JSON.stringify(value))
  } catch (e) {
    console.error(
      `[useLocalStorage] Error setting localStorage key ${key}:`,
      e
    )
  }
}

function removeLocalStorageValue(key: string): void {
  if (typeof window === 'undefined') {
    return
  }

  try {
    window.localStorage.removeItem(key)
  } catch (e) {
    console.error(
      `[useLocalStorage] Error removing localStorage key ${key}:`,
      e
    )
  }
}

interface Config {
  prefix?: string
}

export const configUseLocalStorage = ({ prefix }: Config): void => {
  if (prefix !== undefined) KEY_PREFIX = prefix
}

export function useLocalStorage<
  Value,
  InitValue extends (oldValue: Value) => Value = (oldValue: Value) => Value
>(
  key: string,
  defaultValue: Value,
  { prefix }: Config = {}
): [Value, (newValue: Value | InitValue) => void, () => void] {
  const prefixedKey = useMemo(
    () => `${prefix ?? KEY_PREFIX}_${key}`,
    [prefix, key]
  )

  const [value, setStateValue] = useState<Value>(() =>
    getLocalStorageValue(prefixedKey, defaultValue)
  )

  const setValue = useEvent((newValue: Value | InitValue): void => {
    const newValueParsed =
      typeof newValue === 'function' ? (newValue as InitValue)(value) : newValue

    setLocalStorageValue(prefixedKey, newValueParsed)
    window.dispatchEvent(
      new CustomEvent(prefixedKey, { detail: newValueParsed })
    )
  })

  const removeValue = useEvent(() => {
    removeLocalStorageValue(prefixedKey)
    window.dispatchEvent(new CustomEvent(prefixedKey, { detail: undefined }))
  })

  const onEventTrigger = (event: CustomEvent<Value>): void => {
    setStateValue(event.detail)
  }
  useEventListener(prefixedKey, onEventTrigger)

  const onStorage = (event: StorageEvent): void => {
    if (event.key === prefixedKey) {
      setStateValue(
        event.newValue !== null ? JSON.parse(event.newValue) : undefined
      )
    }
  }

  // this event is only triggered in another sessions/windows
  // @see https://developer.mozilla.org/en-US/docs/Web/API/Window/storage_event
  useEventListener('storage', onStorage)

  return [value, setValue, removeValue]
}
