import type {
  PropsWithoutRef,
  ComponentPropsWithRef,
  ForwardRefExoticComponent,
  RefAttributes,
} from 'react'
import { styles as baseIconStyles } from '@mirohq/design-system-base-icon'
import type { iconSymbol } from '@mirohq/design-system-base-icon'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled, theme } from '@mirohq/design-system-stitches'
import { mapKeysToVariants } from '@mirohq/design-system-utils'

export const StyledIcon = styled(Primitive.svg, {
  display: 'inline-flex',
  verticalAlign: 'text-bottom',
  forcedColorAdjust: 'auto',

  variants: {
    ...baseIconStyles,
    color: mapKeysToVariants(theme.colors, 'color'),
    debug: {
      true: {
        color: '#06D834 !important',
      },
      false: {},
    },
  },
})

export type StyledIconProps = ComponentPropsWithRef<typeof StyledIcon>

export interface IconProps extends StyledIconProps {
  /**
   * The icon size.
   * @default 'medium'
   */
  size?: StyledIconProps['size']

  /**
   * The icon color.
   */
  color?: StyledIconProps['color']

  /**
   * `fr` attr is not available on @types/react-dom 16.x
   */
  fr?: string | number | undefined
}

export type IconComponentType = ForwardRefExoticComponent<
  PropsWithoutRef<IconProps> & RefAttributes<SVGSVGElement>
> & {
  [iconSymbol]?: boolean
}
