import type { Booleanish } from '@mirohq/design-system-types'

import { booleanify } from './primitives'

interface Rect {
  top: number
  right: number
  bottom: number
  left: number
}

interface EventPoint {
  clientX: number
  clientY: number
  width?: number
  height?: number
  radiusX?: number
  radiusY?: number
}

export const isOverTarget = (point: EventPoint, target: Element): boolean => {
  const rect = target.getBoundingClientRect()
  const pointRect = getPointClientRect(point)
  return areRectanglesOverlapping(rect, pointRect)
}

export const getPointClientRect = (point: EventPoint): Rect => {
  const width = point.width ?? 0
  const height = point.height ?? 0

  const offsetX = (width !== 0 ? width / 2 : point.radiusX) ?? 0
  const offsetY = (height !== 0 ? height / 2 : point.radiusY) ?? 0

  return {
    top: point.clientY - offsetY,
    right: point.clientX + offsetX,
    bottom: point.clientY + offsetY,
    left: point.clientX - offsetX,
  }
}

export const areRectanglesOverlapping = (a: Rect, b: Rect): boolean => {
  // check if they cannot overlap on x axis
  if (a.left > b.right || b.left > a.right) {
    return false
  }
  // check if they cannot overlap on y axis
  if (a.top > b.bottom || b.top > a.bottom) {
    return false
  }
  return true
}

/**
 * Returns an empty string if the value is truthy, otherwise undefined.
 * It's useful for boolean attributes that dont need explicit 'true' value.
 *
 * @param x - The value to convert to a boolean attribute value.
 * @returns An empty string if the value is truthy, otherwise undefined.
 *
 * @example
 * true  <button data-disabled />
 * @example
 * false <button />
 * @example
 * undefined <button />
 */
export const booleanishAttrValue = (x?: Booleanish): '' | undefined =>
  booleanify(x) ? '' : undefined

/**
 * Joins list of provided ids into a single string.
 * It's useful for string attributes that don't require an explicit 'true' value.
 *
 * @param ids - array of a string ids. Id could be null/undefined or false.
 * False is useful when id should be provided only when the element is rendered
 *
 * @example
 * stringAttrValue('id-1', 'id-2')
 * returns 'id-1 id-2'
 * @example
 * stringAttrValue(undefined, 'id-2')
 * returns 'id-2'
 * @example
 * stringAttrValue('', ' ')
 * returns undefined
 * @example
 * stringAttrValue('id', badgeElement != null && 'badge-id')
 * returns 'id' when badgeElement == null
 * returns 'id badge-id' when badgeElement != null
 * @example
 * stringAttrValue(false, null, undefined)
 * returns undefined
 */
export const stringAttrValue = (
  ...ids: Array<string | undefined | null | false>
): string | undefined => {
  const str = ids
    .filter(x => x != null && x !== false)
    .join(' ')
    .trim()

  return str !== '' ? str : undefined
}
