// Historically the Miro board has added OpenSans (without \s) and Roobert (without PRO).
// Figma fonts use Open Sans (with \s) and Roobert PRO.
// To avoid issues in the Miro board we also add OpenSans as fallback.

const fallback = 'Noto Sans KR, Noto Sans JP, sans-serif'

export const fonts = {
  heading: `Roobert PRO, Roobert, ${fallback}`,
  body: `Noto Sans, OpenSans, ${fallback}`,
} as const

export type Fonts = keyof typeof fonts
