export const iconSymbol: unique symbol = Symbol.for('icon')
export type IconReactElement = React.ReactElement & { [iconSymbol]?: boolean }

export const isIconComponent = <T>(
  iconComponent: T & { [iconSymbol]?: boolean }
): boolean =>
  Boolean(
    iconComponent[iconSymbol] ??
      // @ts-expect-error
      iconComponent?.type?.[iconSymbol]
  )
