import * as tokens from '@mirohq/design-tokens'
import { base } from '@mirohq/design-system-themes'
import type { Theme as DS_Theme } from '@mirohq/design-system-themes'
import merge from 'lodash.merge'

export const theme = {
  'border-widths': tokens.borderWidths,
  colors: merge({}, tokens.colors, base.colors),
  'font-sizes': tokens.fontSizes,
  'font-weights': tokens.fontWeights,
  'line-heights': tokens.lineHeights,
  fonts: tokens.fonts,
  radii: tokens.radii,
  shadows: base.shadows as Required<DS_Theme['shadows']>,
  sizes: tokens.sizes,
  space: tokens.space,
  'stroke-width': tokens.strokeWidths,
  'z-indices': tokens.zIndices,
}

export type Theme = typeof theme
